<template>
    <div v-if="getProject">
        <div class="container">
            <div class="row">
                <div class="col-md-12 mb-4">
                    <b-button to="/projects" variant="link">
                        Назад
                    </b-button>
                </div>
            </div>
            <div class="row mb-4">
                <div class="col-md-6">
                    <div class="billboard" :style="{ backgroundImage: `url(${getProject.img})` }"></div>
                </div>
            </div>
            <div class="row mb-5">
                <div class="card-body">
                    <h4 class="card-title">{{ getProject.title }}</h4>
                    <ul class="pl-3">
                        <li>Город: <b>{{ getProject.params.city }}</b></li>
                        <li>Стадия: <b>{{ getProject.params.stage }}</b> </li>
                        <li>Категория: <b>{{ getProject.params.industry }}</b> </li>
                        <li><b>{{ getProject.params.price }} ₽</b></li>
                    </ul>
                    <p class="card-text">
                        {{ getProject.subtitle }}
                    </p>
                </div>
            </div>
            <div class="row">
                <div class="col-md-12">
                    <div v-html="getProject.description"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapActions, mapState } from 'vuex';

    export default {
        name: "Project",
        methods: {
            ...mapActions([
                'loadProject',
                'toggleLoad'
            ])
        },
        computed: {
            ...mapState({
                'getProject': state => state.project.message
            })
        },
        created() {
            this.toggleLoad(true);
            this.loadProject(this.$route.params.id).finally(() => {
                this.toggleLoad(false)
            });
        }
    }
</script>

<style scoped lang="stylus">
    .billboard {
        height 300px
        background-size cover
        background-position center
        background-repeat no-repeat
        border-radius 12px
        overflow hidden
    }
</style>